// src/pages/Categories.jsx
import React, { useState, useEffect } from 'react';
import {
  Table,
  Button,
  Input,
  Card,
  Space,
  Modal,
  Form,
  Typography,
  Tag,
  Tooltip,
  Popconfirm,
  ColorPicker,
  TreeSelect,
} from 'antd';
import {
  SearchOutlined,
  PlusOutlined,
  EditOutlined,
  DeleteOutlined,
  TagsOutlined,
} from '@ant-design/icons';
import styled from 'styled-components';
import { useAppContext } from '../context/AppContext';

const { Title, Text } = Typography;
const { TextArea } = Input;

// Style for RTL tables
const StyledTable = styled(Table)`
  .ant-table-thead > tr > th {
    text-align: right;
  }

  .ant-table-cell {
    text-align: right;
  }
`;

const StyledCard = styled(Card)`
  margin-bottom: 24px;
`;

const FilterContainer = styled.div`
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  margin-bottom: 24px;
`;

const ActionsColumn = styled.div`
  display: flex;
  justify-content: flex-end;
  gap: 8px;
`;

const ColorTag = styled.div`
  display: inline-block;
  width: 16px;
  height: 16px;
  border-radius: 4px;
  margin-left: 8px;
  background-color: ${(props) => props.color};
  border: 1px solid rgba(0, 0, 0, 0.1);
`;

const Categories = () => {
  const {
    categories,
    addCategory,
    updateCategory,
    deleteCategory,
    books,
    createCategoryModel,
  } = useAppContext();

  // State
  const [form] = Form.useForm();
  const [searchText, setSearchText] = useState('');
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [editingCategory, setEditingCategory] = useState(null);
  const [filteredCategories, setFilteredCategories] = useState([]);

  // Filter categories based on search text
  useEffect(() => {
    if (!searchText.trim()) {
      setFilteredCategories(categories);
      return;
    }

    const lowerSearchText = searchText.toLowerCase();
    const filtered = categories.filter(
      (category) =>
        category.name.toLowerCase().includes(lowerSearchText) ||
        (category.description &&
          category.description.toLowerCase().includes(lowerSearchText))
    );

    setFilteredCategories(filtered);
  }, [categories, searchText]);

  // Get book count for a category
  const getCategoryBookCount = (categoryId) => {
    return books.filter(
      (book) => book.categories && book.categories.includes(categoryId)
    ).length;
  };

  // Get parent category name
  const getParentCategoryName = (parentId) => {
    if (!parentId) return null;
    const parent = categories.find((c) => c.id === parentId);
    return parent ? parent.name : null;
  };

  // Convert categories to TreeSelect data
  const getCategoryTreeData = () => {
    // Root categories (no parent)
    const rootCategories = categories.filter((c) => !c.parent);

    // Function to create tree nodes
    const createTreeNode = (category) => {
      const node = {
        title: category.name,
        value: category.id,
        key: category.id,
      };

      // Add children if this category is a parent to others
      const children = categories.filter((c) => c.parent === category.id);
      if (children.length > 0) {
        node.children = children.map(createTreeNode);
      }

      return node;
    };

    return rootCategories.map(createTreeNode);
  };

  // Modal handlers
  const showModal = (category = null) => {
    setEditingCategory(category);

    if (category) {
      form.setFieldsValue({
        ...category,
        color: category.color || '#1890ff',
      });
    } else {
      form.resetFields();
    }

    setIsModalVisible(true);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
    form.resetFields();
  };

  const handleSubmit = async (values) => {
    // Prevent creating circular parent relationships
    if (editingCategory && values.parent === editingCategory.id) {
      Modal.error({
        title: '',
        content: '      ',
      });
      return;
    }

    if (editingCategory) {
      await updateCategory(editingCategory.id, values);
    } else {
      await addCategory(values);
    }

    setIsModalVisible(false);
    form.resetFields();
  };

  const handleDelete = async (categoryId) => {
    // Check if category has books
    const categoryBooks = books.filter(
      (book) => book.categories && book.categories.includes(categoryId)
    ).length;

    // Check if category has child categories
    const childCategories = categories.filter(
      (cat) => cat.parent === categoryId
    ).length;

    if (categoryBooks > 0 || childCategories > 0) {
      Modal.confirm({
        title: '    ',
        content: `   -${categoryBooks}  -${childCategories}  .       .`,
        okText: '',
        cancelText: null,
        cancelButtonProps: { style: { display: 'none' } },
      });
      return;
    }

    await deleteCategory(categoryId);
  };

  // Table columns
  const columns = [
    {
      title: ' ',
      dataIndex: 'name',
      key: 'name',
      render: (text, record) => (
        <Space>
          <ColorTag color={record.color} />
          <Text strong>{text}</Text>
        </Space>
      ),
    },
    {
      title: '',
      dataIndex: 'description',
      key: 'description',
      render: (text) => text || '-',
      ellipsis: true,
    },
    {
      title: ' ',
      key: 'parent',
      render: (_, record) => {
        const parentName = getParentCategoryName(record.parent);
        return parentName || '-';
      },
    },
    {
      title: '',
      key: 'bookCount',
      render: (_, record) => {
        const count = getCategoryBookCount(record.id);
        return count > 0 ? (
          <Tag color="blue">{count}</Tag>
        ) : (
          <Tag color="default">0</Tag>
        );
      },
    },
    {
      title: '',
      key: 'actions',
      width: 120,
      render: (_, record) => (
        <ActionsColumn>
          <Tooltip title="">
            <Button
              icon={<EditOutlined />}
              type="text"
              onClick={() => showModal(record)}
            />
          </Tooltip>
          <Tooltip title="">
            <Popconfirm
              title="     ?"
              onConfirm={() => handleDelete(record.id)}
              okText=""
              cancelText=""
              placement="topRight"
            >
              <Button icon={<DeleteOutlined />} type="text" danger />
            </Popconfirm>
          </Tooltip>
        </ActionsColumn>
      ),
    },
  ];

  return (
    <div>
      <StyledCard>
        <div
          style={{
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
            marginBottom: 16,
          }}
        >
          <Title level={2}></Title>
          <Button
            type="primary"
            icon={<PlusOutlined />}
            onClick={() => showModal()}
          >
             
          </Button>
        </div>

        <FilterContainer>
          <Input
            placeholder="    "
            prefix={<SearchOutlined />}
            value={searchText}
            onChange={(e) => setSearchText(e.target.value)}
            style={{ width: 300 }}
          />
        </FilterContainer>

        <StyledTable
          dataSource={filteredCategories}
          columns={columns}
          rowKey="id"
          pagination={{
            pageSize: 10,
            showSizeChanger: true,
            showTotal: (total) => `" ${total} `,
          }}
          locale={{
            emptyText: '  ',
          }}
        />
      </StyledCard>

      {/* Category Form Modal */}
      <Modal
        title={editingCategory ? ' ' : '  '}
        open={isModalVisible}
        onCancel={handleCancel}
        footer={null}
        destroyOnClose
      >
        <Form
          form={form}
          layout="vertical"
          onFinish={handleSubmit}
          initialValues={createCategoryModel()}
        >
          <Form.Item
            name="name"
            label=" "
            rules={[{ required: true, message: '    ' }]}
          >
            <Input placeholder="   " />
          </Form.Item>

          <Form.Item name="description" label="">
            <TextArea placeholder="  " rows={3} />
          </Form.Item>

          <Form.Item name="color" label="">
            <ColorPicker />
          </Form.Item>

          <Form.Item name="parent" label=" ">
            <TreeSelect
              treeData={getCategoryTreeData()}
              placeholder="   ()"
              allowClear
              treeDefaultExpandAll
              showSearch
              filterTreeNode={(input, option) =>
                option.title.toLowerCase().indexOf(input.toLowerCase()) >= 0
              }
              disabled={
                editingCategory &&
                categories.some((c) => c.parent === editingCategory.id)
              }
            />
          </Form.Item>

          {/* Footer */}
          <div style={{ display: 'flex', justifyContent: 'flex-end', gap: 16 }}>
            <Button onClick={handleCancel}></Button>
            <Button type="primary" htmlType="submit">
              {editingCategory ? '' : ''}
            </Button>
          </div>
        </Form>
      </Modal>
    </div>
  );
};

export default Categories;
